// Food emoji mappings for fallback images
export const getFoodEmoji = (foodName: string, cuisineType: string, foodType: string): string => {
  const food = foodName.toLowerCase();
  const cuisine = cuisineType.toLowerCase();
  const type = foodType.toLowerCase();

  // Specific food name matches
  if (food.includes('bulgogi') || food.includes('bbq')) return 'ü•©';
  if (food.includes('pizza')) return 'üçï';
  if (food.includes('sushi') || food.includes('sashimi') || food.includes('roll')) return 'üç£';
  if (food.includes('ramen') || food.includes('noodle')) return 'üçú';
  if (food.includes('taco')) return 'üåÆ';
  if (food.includes('pad thai')) return 'üçù';
  if (food.includes('burger')) return 'üçî';
  if (food.includes('pancake')) return 'ü•û';
  if (food.includes('croissant')) return 'ü•ê';
  if (food.includes('latte') || food.includes('coffee')) return '‚òï';
  if (food.includes('tea') && food.includes('green')) return 'üçµ';
  if (food.includes('energy') || food.includes('drink')) return 'ü•§';

  // Cuisine-based matches
  if (cuisine === 'korean') return 'üá∞üá∑';
  if (cuisine === 'japanese') return 'üç±';
  if (cuisine === 'italian') return 'üçù';
  if (cuisine === 'mexican') return 'üå∂Ô∏è';
  if (cuisine === 'thai') return 'üå∂Ô∏è';
  if (cuisine === 'chinese') return 'ü•¢';
  if (cuisine === 'french') return 'ü•ñ';
  if (cuisine === 'indian') return 'üçõ';
  if (cuisine === 'american') return 'üçî';

  // Food type matches
  if (type === 'main dish') return 'üçΩÔ∏è';
  if (type === 'appetizer') return 'ü•ó';
  if (type === 'dessert') return 'üç∞';
  if (type === 'beverage') return 'ü•§';
  if (type === 'soup') return 'üç≤';
  if (type === 'salad') return 'ü•ó';
  if (type === 'side dish') return 'üçö';
  if (type === 'snack') return 'üçø';
  if (type === 'bread') return 'üçû';
  if (type === 'noodles') return 'üçú';
  if (type === 'rice') return 'üçö';
  if (type === 'meat') return 'ü•©';
  if (type === 'seafood') return 'üêü';

  // Default fallback
  return 'üçΩÔ∏è';
};

// Rating-based emoji for Kuchisabishii scale
export const getKuchisabishiEmoji = (rating: number): string => {
  switch (rating) {
    case 1: return 'üòû';
    case 2: return 'üòï';
    case 3: return 'üòê';
    case 4: return 'üòä';
    case 5: return 'üòç';
    default: return 'ü§î';
  }
};

